.header-and-promo {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  min-height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url("../img/main/bg.png");

  @media (max-width: $mobile) {
    background-image: url("../img/main/bg-mob.png");
  }
}
