.form-order {
  padding: 23px 61px 20px 55px;
  max-width: 368px;
  background-color: #ffffff;
  box-sizing: border-box;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 2px;

  @media (max-width: $mobile) {
    padding: 23px 35px 20px 33px;
  }

  & h3 {
    margin: 0 0 12px 0;
    font-weight: bold;
    font-size: 28px;
    line-height: 32px;
    letter-spacing: -0.01em;
    text-align: center;

    @media (max-width: $mobile) {
      margin-bottom: 44px;
    }
  }

  & p {
    margin: 0;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 20px;
    color: #828282;
    text-align: center;
  }

  &__sale {
    margin-bottom: 88px;
    padding-right: 15px;
    font-weight: bold;
    font-size: 22px;
    line-height: 110%;
    text-align: center;
    letter-spacing: -0.01em;
    color: $warning-color;

    @media (max-width: $mobile) {
      margin-bottom: 53px;
      padding-right: 0;
      padding-left: 15px;
      font-weight: bold;
    }

    & span {
      font-style: normal;
      font-weight: 800;
      font-size: 38px;
      line-height: 55px;
    }
  }

  & label {
    display: block;
    margin-bottom: 12px;
    color: $default-color;
    box-sizing: border-box;
  }

  & label[for="check"] {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 35px;

    font-size: 10px;
    line-height: 130%;

    @media (max-width: $desktop) {
      line-height: 190%;
      margin-bottom: 20px;
    }

    @media (max-width: $table) {
      margin-bottom: 28px;
      letter-spacing: 0.04em;
      line-height: 110%;
    }
  }

  & input:valid {
    border-color: green;
  }

  & input:invalid {
    border-color: red;
  }

  & input:required:valid {
    border-color: green;
  }

  & input[type="text"],
  & select {
    padding: 10px;
    width: 100%;
    font-size: 14px;
    line-height: 150%;
    letter-spacing: 0.04em;
    border: 1px solid $border-color;

    border-radius: 2px;
    background-color: #ffffff;
    box-sizing: border-box;

    cursor: pointer;

    &:focus,
    &:hover {
      border: 1px solid #bce0fa;
    }
  }

  & select {
    margin-bottom: 12px;
  }

  & button[type="submit"] {
    display: inline-block;
    vertical-align: middle;
    padding: 18px;
    margin-bottom: 18px;
    width: 100%;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 100%;
    text-align: center;
    text-transform: uppercase;
    border: none;
    color: #ffffff;
    background-color: $warning-color;

    box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.25);
    border-radius: 50px;
    cursor: pointer;

    &:focus,
    &:hover {
      opacity: 0.5;
    }

    @media (max-width: $desktop) {
      margin-top: 10px;
    }

    @media (max-width: $mobile) {
      margin-top: 8px;
    }
  }
}
