.container {
  max-width: 60%;
  width: 100%;
  margin: 0 auto;
  box-sizing: border-box;

  @media (max-width: 1600px) {
    max-width: 80%;
  }

  @media (max-width: 1300px) {
    max-width: 90%;
  }

  @media (max-width: $mobile) {
    max-width: 67%;
  }

  @media (max-width: 320px) {
    max-width: 100%;
  }
}
