.promo {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: start;
  padding-bottom: 50px;
  padding-top: 18px;
  min-height: 100%;

  @media (max-width: $table) {
    padding-top: 8px;
    justify-content: center;
  }

  &__info {
    display: flex;
    flex-wrap: wrap;
    width: 58%;
    padding-left: 12px;
    box-sizing: border-box;
    position: relative;

    @media (max-width: $table) {
      width: 100%;
      padding-left: 0;
      margin-bottom: 30px;
    }

    & h1 {
      margin: 0;
      padding: 0;
      width: 100%;
      font-style: normal;
      font-weight: 900;
      font-size: 56px;
      line-height: 68px;
      text-transform: uppercase;
      text-shadow: 2px 2px 0 #ffffff;
      color: $primary-color;

      @media (max-width: 1200px) {
        font-size: 46px;
      }

      @media (max-width: $table) {
        font-size: 32px;
        line-height: 40px;
        text-align: center;
      }

      & span {
        &:last-child {
          display: block;
        }

        @media (max-width: $table) {
          display: block;
        }
      }
    }

    & ul {
      padding: 65px 0 0 0;
      margin: 0;
      list-style: none;

      width: 55%;

      @media (max-width: $table) {
        max-width: 390px;
        margin: 0 auto;
        padding-top: 32px;
        padding-bottom: 20px;
        padding-left: 12px;
        width: 80%;
      }
    }

    & li {
      position: relative;
      padding-left: 75px;
      min-height: 50px;
      margin-bottom: 49px;

      @media (max-width: $table) {
        margin-bottom: 20px;
        font-size: 18px;
        line-height: 24px;

        & span {
          display: block;
        }
      }

      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 50px;
        height: 50px;
        background-repeat: no-repeat;
        background-size: contain;
        background-position: 0 0;
        background-image: url("../img/icons/icon-checksvg.svg");

        @media (max-width: $table) {
          top: 6px;
          width: 55px;
          height: 55px;
        }
      }
    }

    & img {
      position: absolute;
      bottom: 40px;
      right: 0;
      width: auto;
      object-fit: contain;

      @media (max-width: $table) {
        width: 100%;
        position: relative;
        bottom: 0;
      }
    }
  }

  &__form {
    width: 42%;
    margin-top: 15px;

    @media (max-width: $table) {
      width: 100%;
      margin-top: 45px;
    }
  }
}
