.logo {
  position: relative;
  display: block;
  max-width: 306px;

  &::before {
    z-index: 0;
    position: absolute;
    content: "";
    top: 0;
    left: -30%;
    width: 130%;
    height: 130%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100%;
    background-image: url("../img/logo-bg.png");
  }

  @media (max-width: $table) {
    margin: 0 auto;

    &::before {
      background-image: url("");
    }
  }

  & img {
    z-index: 2;
    position: relative;
    width: 200px;
    height: 64px;
  }
}
